<!-- 【统计信息】管理页面 -->
<template>
  <div class="body">
    <el-row>
      <el-col span="12">
        <echarts-line title="各类数据数量统计图" ref="echartsLine"></echarts-line>
      </el-col>
      <el-col span="12">
        <echarts-pie title="各类数据数量占比图" ref="echartsPie"></echarts-pie>
      </el-col>
    </el-row>
    <echarts-bend title="每日数据数量变化图" ref="echartsBend"></echarts-bend>
  </div>
</template>

<script>
    export default {
      data() {
        return {
          loading: true,//是否加载完成
          selectedData: [],//勾选数据
          total: 0, //记录数
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            name: ""
          }
        };
      },
      mounted() {
        //初始化数据
        this.getList();
      },
      computed: {
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/admin/category/getCount', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              const xLine = res.data.line.xData;
              const yLine = res.data.line.yData;
              const pie = res.data.pie.data;
              const xBend = res.data.bend.xData;
              const yBend = res.data.bend.yData;
              this.$refs.echartsLine.drawLine(xLine,yLine);
              this.$refs.echartsPie.drawPie(pie);
              this.$refs.echartsBend.drawBend(xBend,yBend);
            }
          });
        },
        //重置查询
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //打开弹窗
        openDialog(option) {
          this.$refs.countDialog.open(option);
        },
        //选择页数
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //获取勾选数据
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //批量删除
        handleDeleteAll() {
          const that = this;
          that.showConfirm('确定删除选中的数据吗?', function () {
            if (that.selectedData.length > 0) {
              let ids = that.selectedData.map(function (v) {return v.id});
              that.$request.doGet('/admin/count/delAll', {ids: ids}).then(res => {
                if (res.isOk) {
                  that.showSuccess("删除成功");
                  that.getList();
                }
              });
            } else {
              that.showWarn("至少选择一条数据！");
            }
          });
        },
        //选中行删除
        handleDelete(id) {
          const that = this;
          that.showConfirm('确定删除选中的数据吗?', function () {
            that.$request.doGet('/admin/count/delete?id=' + id).then(res => {
              if (res.isOk) {
                that.showSuccess("删除成功");
                that.getList();
              }
            });
          });
        }
      }
    };
</script>



