<!-- 【卡片信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="subject" ref="subjectForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="subject.id"/>
        </el-form-item>
        <el-form-item label="图片" prop="picture">
          <sui-photo type="subject" v-model="subject.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="subject.title" placeholder="请输入标题" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="subject.name" placeholder="请输入名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="分类" prop="categoryId">
          <el-select v-model="subject.categoryId" placeholder="请选择分类" :disabled="dialog.disabled">
            <el-option v-for="category in categoryDict" :key="category.id" :value="category.id" :label="category.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input type="textarea" v-model="subject.content" placeholder="请输入内容" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "subjectForm",
    data() {
      return {
        categoryDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        subject: {
          picture: "",
          title: "",
          name: "",
          categoryId: "",
          content: ""
        },
        //表单校验
        rules: {
          picture: [{required: true, message: '请选择图片', trigger: 'blur'}],
          title: [{required: true, message: '请输入标题', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'subject',tableName:'subject',column:'title',message:'标题已存在',trigger:'blur'}],
          name: [{required: true, message: '请输入名称', trigger: 'blur'}],
          categoryId: [{required: true, message: '请选择分类', trigger: 'blur'}],
          content: [{required: true, message: '请输入内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //【分类】
      getCategoryDict() {
        this.$request.doGet('/admin/category/getList').then(res => {
          if (res.isOk) {
            this.categoryDict = res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getCategoryDict();//加载【分类】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSubject(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getSubject(id) {
        this.$request.doGet("/admin/subject/get?id=" + id).then(res => {
          if (res.isOk) {
            this.subject = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.subjectForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/subject/sub', that.subject).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.subjectForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



